.plus-app.plus-app-2017-04-kouluruokailu {
  /* For old browsers */
  display: block;
  /* Search styles */
  /* General styles */
}
.plus-app.plus-app-2017-04-kouluruokailu h3 {
  font-weight: bold;
}
.plus-app.plus-app-2017-04-kouluruokailu table {
  counter-reset: rowNumber;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody {
  border: 0;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr {
  counter-increment: rowNumber;
  background-color: #fff;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr:hover {
  background-color: #fff;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr:nth-child(even) {
  background-color: #fff;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr:nth-child(even) .sorting_1 {
  background-color: #f1f1f1;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr:nth-child(odd) {
  background-color: #f1f1f1;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr:nth-child(odd) .sorting_1 {
  background-color: #e9e9e9;
}
.plus-app.plus-app-2017-04-kouluruokailu table tbody tr td {
  border: 0;
  border-bottom: 1px solid #ccc;
  vertical-align: middle;
  padding: 8px 8px;
  font-size: 14px;
  line-height: 16.8px;
}
.plus-app.plus-app-2017-04-kouluruokailu table thead tr {
  border-bottom: 2px solid #999;
}
.plus-app.plus-app-2017-04-kouluruokailu table thead tr:hover {
  background-color: #fff;
}
.plus-app.plus-app-2017-04-kouluruokailu table thead tr th {
  border: 0;
  cursor: pointer;
  outline: none;
  vertical-align: middle;
  text-align: left;
  padding: 8px 10px;
}
.plus-app.plus-app-2017-04-kouluruokailu table thead tr th span {
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
}
.plus-app.plus-app-2017-04-kouluruokailu table thead tr th:hover {
  background-color: #fff;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container {
  font-size: 14px;
  margin-bottom: 20px;
  min-height: 20px;
  overflow-x: auto;
  /* Table styles */
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTables_filter {
  margin-bottom: 30px;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTables_filter input {
  box-sizing: border-box;
  background: #f1f1f1 url('../img/search.png') no-repeat 20px 50%;
  border: 0;
  font-size: 1.75em;
  height: 50px;
  height: 70px;
  margin: 0;
  outline: none;
  overflow: hidden;
  padding-left: 20px;
  padding: 0 10px 0 80px;
  white-space: nowrap;
  width: 100%;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable {
  width: 100%;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead {
  width: 100%;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting_asc span,
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting_desc span,
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting span {
  background-position: 100% 50%;
  background-repeat: no-repeat;
  display: inline-block;
  padding-right: 16px;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting_asc span {
  background-image: url('../img/sort_asc.svg');
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting_desc span {
  background-image: url('../img/sort_desc.svg');
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTable thead .sorting span {
  background-image: url('../img/sort.svg');
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .dataTables_info {
  display: none;
}
.plus-app.plus-app-2017-04-kouluruokailu .table_container .plus,
.plus-app.plus-app-2017-04-kouluruokailu .table_container .percent,
.plus-app.plus-app-2017-04-kouluruokailu .table_container .number {
  text-align: right;
}
.plus-app.plus-app-2017-04-kouluruokailu .clear_both {
  clear: both;
}
.plus-app.plus-app-2017-04-kouluruokailu .hidden {
  display: none;
}
.plus-app.plus-app-2017-04-kouluruokailu .nowrap {
  white-space: nowrap;
}
